				title: 'picker',
				arraylist: [],
				index: 0,
			
			}
		},
		onLoad() {
			var token = uni.getStorageSync('access_token');
			uni.request({
				url:'http://172.17.36.23:8085/cloudyee_saas_war_exploded/oauth2/resources',
				method:"POST",
				header:{
					'content-type': 'application/json'
				},
				data: {
				 interface_code: "getTradeAreaList",
				 interface_parameters: "[]",
				 access_token: token,
				},
				success: (res) => {
					console.log("成功666");
					console.log("res ==>", res);
						this.arraylist = res.data.Result;
						console.log(this.arraylist,"888");
						console.log(res.data,"000");
				},
				fail: (err) => {
					console.log("失败555");
						console.log("err ==>", err);
				}
			})
		},
		computed: {
			
		},
		methods: {
			bindPickerChange: function(e) {
				console.log('选择值为'+ e.detail.value)
				this.index = e.detail.value
			},
			
			search(res) {
				uni.showToast({
					title: '搜索：' + res.value,
					icon: 'none'
				})
			},
			input(res) {
				console.log('----input:', res)
			},
			clear(res) {
				uni.showToast({
					title: 'clear事件，清除值为：' + res.value,
					icon: 'none'
				})
			},
			blur(res) {
				uni.showToast({
					title: 'blur事件，输入值为：' + res.value,
					icon: 'none'
				})
			},
			focus(e) {
				uni.showToast({
					title: 'focus事件，输出值为：' + e.value,
					icon: 'none'
				})
			},
			cancel(res) {
				uni.showToast({
					title: '点击取消，输入值为：' + res.value,
					icon: 'none'
				})
			}
		},
